import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"


const Ubuntu = () => {
    return (
        <Layout>
            <SEO
                title="Vitamina A"
                meta="Toda la información que necesitas sobre la vitamina A. ¿Qué es? ¿Qué funciones hace? ¿Síntomas de déficit de vitamina A?"
                description="Toda la información que necesitas sobre la vitamina A. ¿Qué es? ¿Qué funciones hace? ¿Síntomas de déficit de vitamina A?"
                lang="es"
            />
            <div style={{height: `50px`}}>
            </div>
            <h2>Que es la vitamina A</h2>
            <p>La vitamina A es una vitamina liposoluble que se almacena en el h&iacute;gado. La vitamina A
                est&aacute; involucrada en la funci&oacute;n inmunol&oacute;gica, la visi&oacute;n, la
                reproducci&oacute;n y la comunicaci&oacute;n celular .La vitamina A es cr&iacute;tica para la
                visi&oacute;n como un componente esencial de la rodopsina, una prote&iacute;na que absorbe la luz en los
                receptores de la retina, y porque apoya la diferenciaci&oacute;n y el funcionamiento normal de las
                membranas conjuntivas y la c&oacute;rnea .La vitamina A tambi&eacute;n apoya el crecimiento y la
                diferenciaci&oacute;n celular, jugando un papel cr&iacute;tico en la formaci&oacute;n y mantenimiento
                normal del coraz&oacute;n, los pulmones, los ri&ntilde;ones y otros &oacute;rganos .</p>
            <p></p>
            <h2>Vitamina A para que sirve</h2>
            <p>La vitamina A ayuda a una buena visi&oacute;n a la salud de los ojos, estimula la producci&oacute;n y la
                actividad de los gl&oacute;bulos blancos, participa en la remodelaci&oacute;n de los huesos, ayuda a
                mantener sanas las c&eacute;lulas endoteliales (las que recubren las superficies interiores del cuerpo)
                y regula el crecimiento y la divisi&oacute;n de las c&eacute;lulas, tal como se necesita para la
                reproducci&oacute;n.</p>
            <p>Es s&oacute;lo un semi-mito que comer zanahorias te ayudar&aacute; a ver en la oscuridad. El principal
                nutriente de la zanahoria, el betacaroteno (responsable del caracter&iacute;stico color naranja de esta
                ra&iacute;z vegetal), es un precursor de la vitamina A y ayuda a tus ojos a adaptarse en condiciones de
                oscuridad. La vitamina A no puede darte superpoderes de visi&oacute;n nocturna o curar tu dependencia de
                las lentes de contacto, pero comer una cantidad adecuada ayudar&aacute; a la salud de los ojos.</p>
            <p></p>
            <p></p>
            <h2>Alimentos con vitamina A</h2>
            <p></p>
            <ul>
                <li>Aceite de hígado de bacalao</li>
                <li>Huevos</li>
                <li>Cereales de desayuno enriquecidos</li>
                <li>Leche descremada fortificada</li>
                <li>Las verduras y frutas anaranjadas y amarillas</li>
                <li>Otras fuentes de betacaroteno como el br&oacute;coli, las espinacas y la mayor&iacute;a de las
                    verduras de hoja verde oscura.
                </li>
            </ul>


            <p>Cuanto m&aacute;s profundo sea el color de una fruta o verdura, mayor ser&aacute; la cantidad de
                betacaroteno. Las fuentes vegetales de betacaroteno son libres de grasa y colesterol. Su
                absorci&oacute;n mejora si estas fuentes se comen con una grasa.</p>
            <p></p>
            <p></p>
            <p>Las dos formas principales de vitamina A en la dieta humana son la vitamina A preformada
                (retinol, &eacute;steres de retinol) y los carotenoides de provitamina A como el betacaroteno que se
                convierten en retinol. La vitamina A preformada proviene de productos animales, alimentos fortificados y
                suplementos vitam&iacute;nicos. Los carotenoides se encuentran de forma natural en los alimentos
                vegetales. Hay otros tipos de carotenoides que se encuentran en los alimentos que no se convierten en
                vitamina A pero que tienen propiedades beneficiosas para la salud; entre ellos se encuentran el
                licopeno, la lute&iacute;na y la zeaxantina.</p>
            <p></p>
            <h3>Vitamina A alimentos animales (Retinol)</h3>
            <p>La vitamina A preformada se encuentra en productos animales como la carne, el pescado, las aves y los
                productos l&aacute;cteos. Sin embargo, muchas de estas fuentes, excepto la leche descremada fortificada
                con vitamina A, son altas en grasas saturadas y colesterol.</p>
            <p>El Retinol es la forma de vitamina A que se absorbe al comer fuentes de alimentos animales.</p>
            <p>El retinol (vitamina A) es utilizado en los productos cosmeticos para retrasar el envejecimiento de la
                piel y la mejora de esta.&nbsp;</p>
            <p></p>
            <h3>Vitamina A alimentos vegetales (Caroenoides)</h3>
            <p>La provitamina A se encuentra en alimentos vegetales como frutas y verduras. El tipo m&aacute;s
                com&uacute;n de provitamina A es el betacaroteno.</p>
            <p>El betacaroteno es un caratonoide.Los carotenoides son colorantes (pigmentos) de color oscuro. Se
                encuentran en alimentos vegetales que pueden convertirse en una forma activa de vitamina A. Hay
                m&aacute;s de 500 carotenoides conocidos.</p>
            <p>El betacaroteno es un antioxidante. Los antioxidantes protegen las c&eacute;lulas del da&ntilde;o causado
                por las sustancias llamadas radicales libres. La zanahoria como hemos comentado anteriormente es el
                alimento con mas carotenoides, por eso es un alimentado tan recomendao para obtener vitamina a.</p>
            <p></p>
            <h2>Efectos secundarios&nbsp; de la vitamina A</h2>
            <p></p>
            <h3>Deficiencia</h3>
            <p></p>
            <ul>
                <li>Si no recibes suficiente vitamina A, tienes m&aacute;s riesgo de problemas oculares como:</li>
                <li>Ceguera nocturna reversible</li>
                <li>Da&ntilde;o corneal no reversible conocido como xeroftalmia</li>
                <li>La falta de vitamina A puede provocar hiperqueratosis o piel seca y escamosa.</li>
            </ul>
            <p></p>
            <p></p>
            <h3>Exceso</h3>
            <p></p>
            <p>Si tomas demasiada vitamina A, puedes enfermarte.</p>
            <p>Las grandes dosis de vitamina A tambi&eacute;n pueden causar defectos de nacimiento.</p>
            <p>Los beb&eacute;s y los ni&ntilde;os son m&aacute;s sensibles a la vitamina A. Pueden enfermarse
                despu&eacute;s de tomar dosis m&aacute;s peque&ntilde;as de vitamina A o productos que la contengan,
                como el retinol (que se encuentra en las cremas para la piel).</p>
            <p>Las grandes cantidades de betacaroteno no le har&aacute;n enfermar. Sin embargo, las cantidades elevadas
                de betacaroteno pueden hacer que la piel se vuelva amarilla o anaranjada. El color de la piel
                volver&aacute; a ser normal una vez que usted reduzca su consumo de betacaroteno.</p>
            <p></p>
        </Layout>
    )
}

export default Ubuntu
